import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
const columns = [
  {
    dataField: "no",
    text: "No",
    footer: "",
  },
  {
    dataField: "no_kirim",
    text: "Nomor Kirim",
    footer: "",
  },
  {
    dataField: "kode_tukang",
    text: "Kode Tukang",
    footer: "",
  },
  {
    dataField: "kadar_cetak",
    text: "Kadar Cetak(%)",
    footer: "",
  },
  {
    dataField: "bruto",
    text: "Bruto(Gr)",
    footer: (columnData) => columnData.reduce((acc, item) => acc + item, 0),
  },
  {
    dataField: "netto_terima",
    text: "Netto Terima(Gr)",
    footer: (columnData) => columnData.reduce((acc, item) => acc + item, 0),
  },
  {
    dataField: "netto_kirim",
    text: "Netto Kirim(Gr)",
    footer: (columnData) => columnData.reduce((acc, item) => acc + item, 0),
  },
  {
    dataField: "netto_selisih",
    text: "Netto Selisih(Gr)",
    footer: (columnData) => columnData.reduce((acc, item) => acc + item, 0),
  },
];

class TerimaMasak extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      no: 0,
      no_kirim: "",
      kode_barang: "",
      kadar_cetak: "",
      bruto: 0,
      netto_terima: 0,
      netto_selisih: 0,
      netto_kirim: 0,
      status: "",
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    var data = {
      no: parseInt(this.state.no) + 1,
      no_kirim: this.state.no_kirim,
      kode_barang: this.state.kode_barang,
      kadar_cetak: this.state.kadar_cetak,
      bruto: parseInt(this.state.bruto),
      netto_terima: parseInt(this.state.netto_terima),
      netto_kirim: parseInt(this.state.netto_kirim),
      netto_selisih: parseInt(this.state.netto_selisih),
    };
    var array = this.state.products;
    array.push(data);
    alert(JSON.stringify(this.state.products));
    this.setState({ status: "berhasil" });
  };
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Terima Masak
                </h5>
                <div className="row">
                  <div className="col-md-3 col-lg-3 form-group">
                    <label htmlFor="">Tanggal</label>
                    <input
                      type="date"
                      id="tanggal"
                      name="tanggal"
                      className="form-control"
                      onChange={this.inputChange("tanggal")}
                    />
                  </div>
                  <div className="col-md-9 col-lg-9"></div>
                  <div className="col-md-3 col-lg-3 form-group">
                    <label htmlFor="">Nomor Kirim</label>
                    <input
                      type="text"
                      id="no_kirim"
                      name="no_kirim"
                      className="form-control"
                      onChange={this.inputChange("no_kirim")}
                    />
                  </div>
                  <div className="col-md-3 col-lg-3 form-group">
                    <label htmlFor="">Kode Barang</label>
                    <select
                      type="text"
                      id="kode_barang"
                      name="kode_barang"
                      className="form-control"
                      onChange={this.inputChange("kode_barang")}
                    >
                      <option value="KD001">KD01</option>
                      <option value="KD002">KD002</option>
                    </select>
                  </div>
                  <div className="col-md-3 col-lg-3 form-group">
                    <label htmlFor="">Kadar Cetak</label>
                    <input
                      type="text"
                      id="kadar_cetak"
                      name="kadar_cetak"
                      className="form-control"
                      onChange={this.inputChange("kadar_cetak")}
                    />
                  </div>
                  <div className="col-md-3 col-lg-3 form-group">
                    <label htmlFor="">Bruto</label>
                    <input
                      type="text"
                      id="bruto"
                      name="bruto"
                      className="form-control"
                      onChange={this.inputChange("bruto")}
                    />
                  </div>
                  <div className="col-md-3 col-lg-3 form-group">
                    <label htmlFor="">Netto Terima(Gr)</label>
                    <input
                      type="text"
                      id="netto_terima"
                      name="netto_terima"
                      className="form-control"
                      onChange={this.inputChange("netto_terima")}
                    />
                  </div>
                  <div className="col-md-3 col-lg-3 form-group">
                    <label htmlFor="">Netto Kirim(Gr)</label>
                    <input
                      type="text"
                      id="netto_kirim"
                      name="netto_kirim"
                      className="form-control"
                      onChange={this.inputChange("netto_kirim")}
                    />
                  </div>
                  <div className="col-md-3 col-lg-3 form-group">
                    <label htmlFor="">Netto Selisih(Gr)</label>
                    <input
                      type="text"
                      id="netto_selisih"
                      name="netto_selisih"
                      className="form-control"
                      onChange={this.inputChange("netto_selisih")}
                    />
                  </div>
                  <div className="col-lg-12 col-md-12 mb-3 mt-3">
                    <div className="text-right">
                      <button className="btn btn-primary" onClick={this.submit}>
                        Tambah Data
                      </button>
                    </div>
                  </div>
                </div>
                <BootstrapTable
                  keyField="id"
                  data={this.state.products}
                  columns={columns}
                  bordered={true}
                  headerClasses="header-table"
                />
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button className="btn btn-primary">Simpan Data</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default TerimaMasak;
